@import '~node_modules/modern-normalize/modern-normalize.css';

a {
    color: inherit;
    text-decoration: none;
  }
  
  ul {
    padding: 0;
    margin: 0;
    list-style: none;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    padding: 0;
    margin: 0;
  }
  
  img {
    display: block;
    width: 100%;
    height: auto;
  }
  
  button {
    display: inline-block;
    border: none;
    cursor: pointer;
    margin: 0;
    padding: 0;
    background-color: transparent;
  }